import React, { useState } from 'react';
import Layout from '../components/layout/Layout';
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  Tab,
  Tabs,
  Avatar,
  Button,
  Paper,
  Chip,
  TextField,
  InputAdornment
} from '@mui/material';
import {
  Forum,
  Group,
  EmojiEvents,
  TrendingUp,
  Favorite,
  Comment,
  Share,
  Search,
  Add
} from '@mui/icons-material';

const Community = () => {
  const [tabValue, setTabValue] = useState(0);

  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  };

  return (
    <Layout>
      <Box
        sx={{
          minHeight: '100vh',
          background: 'linear-gradient(135deg, #1e40af 0%, #1e3a8a 50%, #1e293b 100%)',
          py: 4
        }}
      >
      <Container maxWidth="lg">
        <Typography variant="h4" sx={{ color: 'white', mb: 4, textAlign: 'center', fontWeight: 'bold' }}>
          Cng ng Hc Tp
        </Typography>

        <Grid container spacing={3} sx={{ mb: 4 }}>
          <Grid item xs={12} md={3}>
            <Card>
              <CardContent sx={{ textAlign: 'center' }}>
                <Forum sx={{ fontSize: 40, color: '#1e40af', mb: 1 }} />
                <Typography variant="h6">1,234</Typography>
                <Typography variant="body2" color="text.secondary">Bi vit</Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} md={3}>
            <Card>
              <CardContent sx={{ textAlign: 'center' }}>
                <Group sx={{ fontSize: 40, color: '#1e40af', mb: 1 }} />
                <Typography variant="h6">567</Typography>
                <Typography variant="body2" color="text.secondary">Thnh vin</Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} md={3}>
            <Card>
              <CardContent sx={{ textAlign: 'center' }}>
                <EmojiEvents sx={{ fontSize: 40, color: '#FFD700', mb: 1 }} />
                <Typography variant="h6">89</Typography>
                <Typography variant="body2" color="text.secondary">Cu chuyn thnh cng</Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} md={3}>
            <Card>
              <CardContent sx={{ textAlign: 'center' }}>
                <TrendingUp sx={{ fontSize: 40, color: '#10B981', mb: 1 }} />
                <Typography variant="h6">98%</Typography>
                <Typography variant="body2" color="text.secondary">T l hi lng</Typography>
              </CardContent>
            </Card>
          </Grid>
        </Grid>

        <Paper sx={{ borderRadius: 2 }}>
          <Tabs
            value={tabValue}
            onChange={handleTabChange}
            variant="fullWidth"
            sx={{
              '& .MuiTab-root': {
                textTransform: 'none',
                fontSize: '1rem',
                fontWeight: 'bold'
              }
            }}
          >
            <Tab icon={<Forum />} label="Din n" />
            <Tab icon={<Group />} label="Nhm Hc Tp" />
            <Tab icon={<EmojiEvents />} label="Thnh Cng" />
          </Tabs>

          <Box sx={{ p: 3 }}>
            {tabValue === 0 && (
              <Box>
                <Typography variant="h6" sx={{ mb: 2 }}>Din n Tho Lun</Typography>
                <Card sx={{ mb: 2 }}>
                  <CardContent>
                    <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                      <Avatar sx={{ mr: 2 }} />
                      <Box>
                        <Typography variant="subtitle1" fontWeight="bold">Minh Tun</Typography>
                        <Typography variant="body2" color="text.secondary">2 gi trc</Typography>
                      </Box>
                    </Box>
                    <Typography variant="h6" sx={{ mb: 1 }}>
                      Cch tm gia s Ton hiu qu cho lp 10?
                    </Typography>
                    <Typography variant="body2" sx={{ mb: 2 }}>
                      Mnh ang cn tm gia s Ton cho em, ai c kinh nghim chia s vi mnh...
                    </Typography>
                    <Box sx={{ display: 'flex', gap: 1, mb: 2 }}>
                      <Chip label="Ton hc" size="small" />
                      <Chip label="Lp 10" size="small" />
                    </Box>
                    <Box sx={{ display: 'flex', gap: 2 }}>
                      <Button startIcon={<Favorite />} size="small">24</Button>
                      <Button startIcon={<Comment />} size="small">8</Button>
                      <Button startIcon={<Share />} size="small">Chia s</Button>
                    </Box>
                  </CardContent>
                </Card>
              </Box>
            )}
            {tabValue === 1 && (
              <Box>
                <Typography variant="h6" sx={{ mb: 2 }}>Nhm Hc Tp</Typography>
                <Grid container spacing={2}>
                  <Grid item xs={12} md={6}>
                    <Card>
                      <CardContent>
                        <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                          <Avatar sx={{ mr: 2, width: 60, height: 60 }} />
                          <Box>
                            <Typography variant="h6">Nhm n Thi i Hc 2024</Typography>
                            <Typography variant="body2" color="text.secondary">
                              156 thnh vin  Tng hp
                            </Typography>
                          </Box>
                        </Box>
                        <Typography variant="body2" sx={{ mb: 2 }}>
                          Cng nhau n tp v chia s kin thc cho k thi i hc
                        </Typography>
                        <Button variant="contained" fullWidth>Tham gia nhm</Button>
                      </CardContent>
                    </Card>
                  </Grid>
                </Grid>
              </Box>
            )}
            {tabValue === 2 && (
              <Box>
                <Typography variant="h6" sx={{ mb: 2 }}>Cu Chuyn Thnh Cng</Typography>
                <Card sx={{ mb: 2 }}>
                  <CardContent>
                    <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                      <EmojiEvents sx={{ color: '#FFD700', mr: 1 }} />
                      <Typography variant="h6" color="primary">
                        Tng t 6.5 ln 8.5 im Ton
                      </Typography>
                    </Box>
                    <Typography variant="subtitle1" sx={{ mb: 1 }}>
                      Hc sinh: Nguyn Vn A
                    </Typography>
                    <Typography variant="body2" sx={{ mb: 2 }}>
                      Sau 3 thng hc vi thy Minh, em  ci thin ng k im s...
                    </Typography>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                      <Typography variant="body2" color="text.secondary">
                        Gia s: Thy Minh c  Ton hc
                      </Typography>
                      <Button size="small">Xem chi tit</Button>
                    </Box>
                  </CardContent>
                </Card>
              </Box>
            )}
          </Box>
        </Paper>
      </Container>
    </Box>
    </Layout>
  );
};

export default Community;