import { useState } from 'react';
import { X, Clock, MapPin, User, DollarSign } from 'lucide-react';
import { users, rooms } from '../../data/mockData';

const BookingModal = ({ timeSlot, onClose, onConfirm }) => {
  const [bookingData, setBookingData] = useState({
    subject: '',
    duration: 60,
    location: '',
    notes: '',
    sessionType: 'online',
    roomId: ''
  });

  const [errors, setErrors] = useState({});

  // Get tutor info
  const tutor = users.find(t => t.id === timeSlot?.tutorId && t.role === 'tutor');

  const handleSubmit = (e) => {
    e.preventDefault();
    
    // Validation
    const newErrors = {};
    if (!bookingData.subject.trim()) {
      newErrors.subject = 'Vui lng nhp mn hc';
    }
    if (!bookingData.duration || bookingData.duration < 30) {
      newErrors.duration = 'Thi lng ti thiu 30 pht';
    }
    if (bookingData.sessionType === 'offline' && !bookingData.location.trim() && !bookingData.roomId) {
      newErrors.location = 'Vui lng chn a im hc';
    }

    if (Object.keys(newErrors).length > 0) {
      setErrors(newErrors);
      return;
    }

    // Calculate price
    const pricePerHour = tutor?.hourlyRate || 150000;
    const totalPrice = (pricePerHour * bookingData.duration) / 60;

    const finalBookingData = {
      ...bookingData,
      date: timeSlot.date,
      time: timeSlot.time,
      tutorId: timeSlot.tutorId,
      tutorName: tutor?.name,
      totalPrice,
      status: 'pending'
    };

    onConfirm(finalBookingData);
  };

  const durationOptions = [
    { value: 30, label: '30 pht' },
    { value: 60, label: '1 gi' },
    { value: 90, label: '1.5 gi' },
    { value: 120, label: '2 gi' },
    { value: 150, label: '2.5 gi' },
    { value: 180, label: '3 gi' }
  ];

  const calculatePrice = () => {
    const pricePerHour = tutor?.hourlyRate || 150000;
    return (pricePerHour * bookingData.duration) / 60;
  };

  const formatCurrency = (amount) => {
    return new Intl.NumberFormat('vi-VN', {
      style: 'currency',
      currency: 'VND'
    }).format(amount);
  };

  if (!timeSlot) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
      <div className="bg-white rounded-lg shadow-xl max-w-md w-full max-h-[90vh] overflow-y-auto">
        {/* Header */}
        <div className="flex items-center justify-between p-6 border-b">
          <h3 className="text-lg font-semibold text-gray-900">
            t lch hc
          </h3>
          <button
            onClick={onClose}
            className="p-1 hover:bg-gray-100 rounded"
          >
            <X className="w-5 h-5" />
          </button>
        </div>

        <form onSubmit={handleSubmit} className="p-6 space-y-4">
          {/* Tutor Info */}
          {tutor && (
            <div className="bg-blue-50 p-4 rounded-lg">
              <div className="flex items-center space-x-3">
                <img
                  src={tutor.avatar}
                  alt={tutor.name}
                  className="w-12 h-12 rounded-full object-cover"
                />
                <div>
                  <h4 className="font-medium text-gray-900">{tutor.name}</h4>
                  <p className="text-sm text-gray-600">{tutor.department}</p>
                  <p className="text-sm font-medium text-blue-600">
                    {formatCurrency(tutor.hourlyRate)}/gi
                  </p>
                </div>
              </div>
            </div>
          )}

          {/* Time Info */}
          <div className="bg-gray-50 p-4 rounded-lg">
            <div className="flex items-center space-x-2 text-sm text-gray-600">
              <Clock className="w-4 h-4" />
              <span>
                {timeSlot.date.toLocaleDateString('vi-VN', { 
                  weekday: 'long',
                  year: 'numeric',
                  month: 'long',
                  day: 'numeric'
                })} lc {timeSlot.time}
              </span>
            </div>
          </div>

          {/* Subject */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Mn hc *
            </label>
            <input
              type="text"
              value={bookingData.subject}
              onChange={(e) => setBookingData({ ...bookingData, subject: e.target.value })}
              className={`w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                errors.subject ? 'border-red-300' : 'border-gray-300'
              }`}
              placeholder="VD: Ton Cao Cp 1, Lp Trnh C++"
            />
            {errors.subject && (
              <p className="text-sm text-red-600 mt-1">{errors.subject}</p>
            )}
          </div>

          {/* Duration */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Thi lng *
            </label>
            <select
              value={bookingData.duration}
              onChange={(e) => setBookingData({ ...bookingData, duration: parseInt(e.target.value) })}
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              {durationOptions.map(option => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </select>
            {errors.duration && (
              <p className="text-sm text-red-600 mt-1">{errors.duration}</p>
            )}
          </div>

          {/* Session Type */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Hnh thc hc
            </label>
            <div className="flex space-x-4">
              <label className="flex items-center">
                <input
                  type="radio"
                  value="online"
                  checked={bookingData.sessionType === 'online'}
                  onChange={(e) => setBookingData({ ...bookingData, sessionType: e.target.value })}
                  className="mr-2"
                />
                Online
              </label>
              <label className="flex items-center">
                <input
                  type="radio"
                  value="offline"
                  checked={bookingData.sessionType === 'offline'}
                  onChange={(e) => setBookingData({ ...bookingData, sessionType: e.target.value })}
                  className="mr-2"
                />
                Trc tip
              </label>
            </div>
          </div>

          {/* Location (if offline) */}
          {bookingData.sessionType === 'offline' && (
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                a im hc *
              </label>
              
              {/* Room Selection */}
              <div className="mb-2">
                <label className="block text-xs text-gray-600 mb-1">
                  Chn phng c sn:
                </label>
                <select
                  value={bookingData.roomId}
                  onChange={(e) => {
                    const room = rooms.find(r => r.id === parseInt(e.target.value));
                    setBookingData({ 
                      ...bookingData, 
                      roomId: e.target.value,
                      location: room ? room.name : bookingData.location
                    });
                  }}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  <option value="">-- Chn phng --</option>
                  {rooms.map(room => (
                    <option key={room.id} value={room.id}>
                      {room.name} ({room.building}) - {room.capacity} ch
                    </option>
                  ))}
                </select>
              </div>

              {/* Or custom location */}
              <div>
                <label className="block text-xs text-gray-600 mb-1">
                  Hoc nhp a im khc:
                </label>
                <input
                  type="text"
                  value={bookingData.location}
                  onChange={(e) => setBookingData({ ...bookingData, location: e.target.value, roomId: '' })}
                  className={`w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                    errors.location ? 'border-red-300' : 'border-gray-300'
                  }`}
                  placeholder="VD: Caf, Th vin, ..."
                />
              </div>
              
              {errors.location && (
                <p className="text-sm text-red-600 mt-1">{errors.location}</p>
              )}
            </div>
          )}

          {/* Notes */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Ghi ch
            </label>
            <textarea
              value={bookingData.notes}
              onChange={(e) => setBookingData({ ...bookingData, notes: e.target.value })}
              rows={3}
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="Ni dung cn h tr, yu cu c bit..."
            />
          </div>

          {/* Price Summary */}
          <div className="bg-green-50 p-4 rounded-lg">
            <div className="flex items-center justify-between">
              <span className="text-sm text-gray-600">Tng chi ph:</span>
              <span className="text-lg font-semibold text-green-600">
                {formatCurrency(calculatePrice())}
              </span>
            </div>
            <p className="text-xs text-gray-500 mt-1">
              {bookingData.duration} pht  {formatCurrency(tutor?.hourlyRate || 150000)}/gi
            </p>
          </div>

          {/* Action Buttons */}
          <div className="flex space-x-3 pt-4">
            <button
              type="button"
              onClick={onClose}
              className="flex-1 px-4 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-gray-500"
            >
              Hy
            </button>
            <button
              type="submit"
              className="flex-1 px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              t lch
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default BookingModal;
